body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minute, .stopwatch__second, .stopwatch__center {
  position: absolute;
}

.stopwatch__minute {
  background-color: #0700ff;
  width: 20vmin;
  height: 3vmin;
  animation: 3600s steps(60, end) infinite rotate-arr;
}

.stopwatch__second {
  background-color: #2c8000;
  width: 38vmin;
  height: 1.5vmin;
  animation: 60s linear infinite rotate-arr;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

@keyframes rotate-arr {
  0% {
    transform: rotate(90deg)translateX(-50%);
  }

  100% {
    transform: rotate(450deg)translateX(-50%);
  }
}

